<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span.text-white 2
      h1 <i>OptiTex</i>

    .row.mb-5.bloque-cus-3
      .col-sm-12.col-lg-7.p-5(data-aos="zoom-in-left")
            p.mb-5 #[i OptiTex] es un sistema #[i CAD/CAM] (Diseño asistido por computador y Manufacturas Asistidas por Computador) 2D y 3D, el cual mejora la rentabilidad en las empresas permitiendo un ahorro de tiempo y minimización del desperdicio de la materia prima, en la elaboración de sus productos. Este #[i software ] es el más utilizado en Colombia actualmente por las empresas manufactureras.
            p.mb-0 En esta unidad temática el enfoque se realiza en patrones únicamente de la línea femenina, sin embargo, cabe resaltar que las herramientas del programa y manejo de interfaz, aplica para cualquier línea, debe tener en cuenta el desarrollo del trabajo manual para aplicarlo de manera digital.
      .col-sm-12.col-lg-4
        figure
          img.img-cus-3.d-none.d-lg-block(src='@/assets/curso/tema2/img01.svg' )


    

    p.mb-5(data-aos="zoom-in-left")  #[i OptiTex] ofrece un paquete completo para suplir este tipo de necesidades, estos  #[i softwares ] son:
    
    .row.my-5(data-aos="zoom-in-left")
      .col-lg-4.d-flex 
        div.bg-img-1.d-flex.flex-column     
          div.p-4.bg-9
            h3.m-0.text-center #[i #[i PDS ] ]
          div.p-5.d-flex.align-items-center(style="height:100%")
            p.text-center Sistema de diseño de patrones, también se pueden anexar por la mesa digitalizadora. 
      .col-lg-4.d-flex
        div.bg-img-1.d-flex.flex-column          
          div.p-4.bg-9
            h3.m-0.text-center #[i Marker ]
          div.p-5.d-flex.align-items-center(style="height:100%")
            p.text-center Es el acomodo de piezas para el corte industrial, se puede realizar de forma manual o automática. Reportes del trazo.
      .col-lg-4.d-flex 
        div.bg-img-1.d-flex.flex-column           
          div.p-4.bg-9
            h3.m-0.text-center #[i RunwayViewer ]
          div.p-5.d-flex.align-items-center(style="height:100%")
            p.text-center Es un sistema de simulación de prendas en un avatar, que puede ser personalizado según la tabla de medidas, se basa en las características de los patrones realizados en #[i PDS ] y en las características precisas de las telas reales.

    p.mb-5(data-aos="zoom-in-left")  A continuación, se describirán las características generales del #[i software ] enfocado en el desarrollo de patrones y escalado de prendas de vestir. 
    .titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h3  Características del #[i software ] #[i OptiTex]
    p.mb-5(data-aos="zoom-in-left")  #[i OptiTex] permite construir de manera dinámica patrones con alta precisión, para ello, emplea una ubicación espacial basados en el plano cartesiano y ángulos que permiten ser muy exactos en el momento de generar cualquier tipo de patrón, partiendo de figuras geométricas o líneas dibujadas. Cabe resaltar que el escalado se realiza en este #[i software ] también.
    p.mb-5(data-aos="zoom-in-left")  Su interfaz es muy intuitiva, generando que el mismo programa le indique el paso a seguir en cada herramienta que se vaya a implementar, escala y modifica patrones ya creados.

    .row.bg-10.py-5.mb-5(data-aos="zoom-in-left")
      .col-lg-1
      .col-lg-10.py-3 
        figure.mb-4
          .titulo-sexto.color-acento-contenido
            h5 Figura 15.
            span Interfaz general de #[i OptiTex] #[i PDS ]
          img(src='@/assets/curso/tema2/img02.png' alt='La imagen expone la interfaz general del software OptiTex PDS. La pantalla está dominada por un área de trabajo grande y amarilla. En la parte superior, hay una barra de herramientas con varias opciones y menús desplegables, como "Archivo", "Editar", "Ver", "Herramientas", y otros. En los bordes superior e izquierdo, hay reglas para la medición y alineación de los elementos en el área de trabajo.')
          figcaption Fuente: SENA 2021
      .col-lg-1
    
    p.mb-5(data-aos="zoom-in-left") <b>Dentro de las características generales de #[i PDS ] se encuentran:</b>

    .row.mb-5(data-aos="zoom-in-left")
      .col-lg-6
        ol.lista-ol--cuadro.lista-ol--separador
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span a
            p.mb-0  Se pueden copiar moldes o patrones de un archivo a otro sin necesidad de generar un nuevo archivo.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span b
            p.mb-0  Cuenta con funciones de guardado en versiones anteriores y se pueden abrir sin ningún inconveniente.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span c
            p.mb-0  Contiene herramientas que propician la construcción de patrones desde cero, transformación, marcaciones, mediciones, escalado y posee características que permite el complemento con los demás paquetes de #[i software. ]
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span d
            p.mb-0  Importación y exportación de diferentes extensiones de patrones realizados en otros  #[i softwares ] de trazo de aplicaciones #[i CAD/CAM].
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span e
            p.mb-0  Interfaz intuitiva y muy fácil de manejar.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span f
            p.mb-0  Se pueden imprimir moldes o patrones directamente a una impresa a la escala que se desee. 
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span g
            p.mb-0  No hay límite de espacio, es demasiado amplio para poder integrar en un mismo archivo muchos patrones. 
      .col-lg-6
        ol.lista-ol--cuadro.lista-ol--separador
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span h
            p.mb-0  Cantidad   de   características: ojales, pinzas, pliegues, costuras, manejo de curvas complicadas, esquinas, cortes, técnicas avanzadas de medición, botones y piquetes.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span i
            p.mb-0  Permite la marcación de cada uno de los nombres de los patrones, descripciones o acotaciones especiales que se necesiten para el ploteo de los moldes.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span j
            p.mb-0  Cuenta con herramientas de texto.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span k
            p.mb-0  El programa que complementa a #[i OptiTex] #[i PDS ] es #[i Marker ], en donde una vez se realice el trazado de los moldes en un espacio que está determinado a partir de anchos de tela y largos de tendido para producción, es posible pasar a hacer el plano que guie el tendido y corte de la producción a confeccionar.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span l
            p.mb-0  Facilidad de creación de reportes.
          li
            .lista-ol--cuadro__vineta.color-acento-botones 
              span m
            p.mb-0  Reporte de información de piezas a Excel para integración con cualquier generador de reportes a su gusto.

    p.mb-5(data-aos="zoom-in-left")  En complemento con lo anterior, dentro de las características generales de #[i Marker ] encontramos:
    
    
