    p.mb-4(data-aos="zoom-in-left")  El programa cuenta con una serie de pestañas integradas de manera secuencial, sin embargo, las vistas se pueden configurar y ajustar según lo considere el patronista.
    p.mb-5(data-aos="zoom-in-left")  Para el manejo e implementación del #[i software ] de patronaje, es necesario tener conocimientos de patronaje y escalado, asociados a la elaboración de básicos.
    
